.card {
    cursor: pointer;
    color: white;
    margin-right: 1em;
    padding-left: 1em;
    padding-top: 0;
    padding-bottom: 0;
}

.card-container .card .half {
    width: 21rem;
    height: 12rem;
    transition: 1.6s;
}

.card-container .card .half.top {
    position: relative;
    background: rgb(18, 17, 17);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(6.5rem);
    height: 9rem;
}

.card-container .card:hover .half.top {
    transform: translateY(0);
}

.card-container .card .half.top .content img {
    width: 21rem;
    height: 13.5rem;
    box-shadow: .7rem 1rem 4rem -1rem rgba(0, 0, 0, 1);
}

img {
    min-width: 21rem;
    min-height: 14rem;
}

p {
    margin-left: .4rem;
    margin-top: 0;
    margin-bottom: 0;
    height: 2em;
}

.card-container .card .half.bottom {
    background: rgb(19, 17, 17);
    box-shadow: 0 1.2rem 3rem rgba(0, 0, 0, 0.75);
    transform: translateY(-6rem);
}

.card-container .card:hover .half.bottom {
    transform: translateY(0);
}

.half.top {
    height: 20rem;
}

.trailer {
    width: 21rem;
    height: 13.5rem;
}